import uniIcons from '../uni-icons/uni-icons.vue'
import uniBadge from '../uni-badge/uni-badge.vue'
import uniSwipeAction from '@/components/uni-swipe-action/uni-swipe-action.vue'
export default {
	name: 'UniListItem',
	components: {
		uniIcons,
		uniBadge,
		uniSwipeAction
	},
	props: {
		title: {
			type: String,
			default: ''
		}, // 列表标题
		note: {
			type: String,
			default: ''
		}, // 列表描述
		disabled: {
			// 是否禁用
			type: [Boolean, String],
			default: false
		},
		showArrow: {
			// 是否显示箭头
			type: [Boolean, String],
			default: true
		},
		showBadge: {
			// 是否显示数字角标
			type: [Boolean, String],
			default: false
		},
		showSwitch: {
			// 是否显示Switch
			type: [Boolean, String],
			default: false
		},
		switchChecked: {
			// Switch是否被选中
			type: [Boolean, String],
			default: false
		},
		badgeText: {
			// badge内容
			type: String,
			default: ''
		},
		badgeType: {
			// badge类型
			type: String,
			default: 'success'
		},
		thumb: {
			// 缩略图
			type: String,
			default: ''
		},
		
		goodsStatus: {
			// 产品状态
			type: Number,
			default: 1
		},
		showExtraIcon: {
			// 是否显示扩展图标
			type: [Boolean, String],
			default: false
		},
		isHaveRight:{
			type: Boolean,
			default: false
			
		},
		extraIcon: {
			type: Object,
			default () {
				return {
					type: 'contact',
					color: '#000000',
					size: 20
				}
			}
		}
	},
	data() {
		return {
			options2: [{
				text: '收藏',
				style: {
					backgroundColor: '#FF6000'
				}
			}, {
				text: '删除',
				style: {
					backgroundColor: '#FF0000'
				}
			}],
		}
	},
	methods: {
		swClick:function(e){
			this.$emit('swClick',e)
		},
		onClick() {
			this.$emit('click')
		},
		onSwitchChange(e) {
			this.$emit('switchChange', e.detail)
		}
	}
}
